import * as fs from 'fs-extra';
import 'jest';
import * as path from 'path';

import React from 'react';
import { renderTags } from '../base-page-utils';

describe('Render htmlHeadIncludeTags', () => {
    it('verify the nonce token is added to inline script', async () => {
        const inlineScript = React.createElement('script', {}, 'asdf');
        const externalScript = React.createElement('script', { src: 'testSrc' });
        const otherTags = React.createElement('meta', { src: 'testSrc' });
        // render tags without nonce.
        const resultWithoutNonce = renderTags([inlineScript, externalScript, otherTags]);
        expect(resultWithoutNonce).toBe(
            '<script data-reactroot="">asdf</script><script src="testSrc" data-reactroot=""></script><meta src="testSrc" data-reactroot=""/>'
        );

        // render tags with nonce.
        const resultWithtNonce = renderTags([inlineScript, externalScript, otherTags], 'hash');
        expect(resultWithtNonce).toBe(
            '<script nonce=hash data-reactroot="">asdf</script><script src="testSrc" data-reactroot=""></script><meta src="testSrc" data-reactroot=""/>'
        );
    });
});
